const wordsAssembler = words => (
  words.map(word => (
    <span>
      {word}
    </span>
  ))
);

const WordDisplay = (props) => {
  const { words, wordIndex } = props;
  const allWords = wordsAssembler(words.slice(wordIndex, words.length));
  return (
    <p>
      {allWords}
    </p>
  );
};

export default WordDisplay;
